<template>
  <div class="nav_bar">
    <div class="nav_container">
      <h1>M.C. MuSiC</h1>
      <ul>
        <router-link to="/track">
          <li
            :class="{'activeRouter': this.activeRoute==='單曲搜尋'}"
            @click="activeRouter(`單曲搜尋`)"
          >單曲搜尋</li>
        </router-link>
        <router-link to="/charts">
          <li :class="{'activeRouter':this.activeRoute==='熱門音榜'}" @click="activeRouter(`熱門音榜`)">熱門音榜</li>
        </router-link>
        <router-link to="/track">
          <li>主題歌單</li>
        </router-link>
      </ul>

      <div class="search_container">
        <div class="search_radio">
          <div class="radio_container">
            <input
              @change="radioChange"
              type="radio"
              id="track"
              value="track"
              v-model="searchType"
              :checked="searchType == 'track'"
            />
            <label for="track">歌曲</label>
          </div>

          <div class="radio_container">
            <input
              @change="radioChange"
              type="radio"
              id="artist"
              value="artist"
              v-model="searchType"
              :checked="searchType == 'artist'"
            />
            <label for="artist">歌手</label>
          </div>
        </div>
        <div class="search_input">
          <input
            @change="setSearchInput"
            @keyup.enter="searchHandler"
            v-model="searchInput"
            type="text"
            placeholder="請輸入關鍵字"
          />
          <button type="button" @click="searchHandler">
            <i class="fas fa-search"></i>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "navBar",
  data() {
    return {
      token: "",
      searchInput: "房東的貓",
      searchType: "artist",
      artistID: "",
      searchItems: "",
      kkboxData: "",
      activeRoute: "單曲搜尋"
    };
  },
  created() {
    this.getToken();
    this.$emit("setSearchInput", this.searchInput);
    this.$emit("setSearchType", this.searchType);
  },
  methods: {
    getToken() {
      const api =
        "https://cors-anywhere.herokuapp.com/https://account.kkbox.com/oauth2/token";
      const config = {
        headers: {
          Accept: "application/x-www-form-urlencoded",
          "Content-Type": "application/x-www-form-urlencoded"
        }
      };
      const oauth = {
        grant_type: "client_credentials",
        client_id: "fb254aff20b31017a2e84e34ed896523",
        client_secret: "d2f7853d7a7e2afd98bc4751f1b3c81a"
      };
      this.$http.post(api, this.$qs.stringify(oauth), config).then(res => {
        this.token = res.data.access_token;
        this.getSearch();
      });
    },
    getSearch() {
      let vm = this;
      const config = {
        headers: {
          Authorization: ` Bearer ${vm.token}`
        }
      };
      this.$http
        .get(
          `https://api.kkbox.com/v1.1/search?q=${vm.searchInput}&type=${vm.searchType}&territory=TW`,
          config
        )
        .then(res => {
          vm.kkboxData = res.data;
          vm.$emit("setkkboxData", vm.kkboxData);
          if (vm.searchType === "artist") {
            vm.artistID = vm.kkboxData.artists.data[0].id;
            vm.$http
              .get(
                `https://api.kkbox.com/v1.1/artists/${vm.artistID}/top-tracks?territory=TW&limit=20`,
                config
              )
              .then(res => {
                vm.searchItems = res.data.data;
                vm.$emit("setsearchItems", vm.searchItems);
              });
          } else if (vm.searchType === "track") {
            vm.$http
              .get(
                `https://api.kkbox.com/v1.1/search?q=${vm.searchInput}&type=${vm.searchType}&territory=TW&limit=20`,
                config
              )
              .then(res => {
                vm.searchItems = res.data.tracks.data;
                vm.$emit("setsearchItems", vm.searchItems);
              });
          }
        });
    },
    searchHandler() {
      const vm = this;
      // vm.searchItems = "";
      // vm.$emit("setsearchItems", vm.searchItems);
      vm.getSearch();
    },
    setSearchInput() {
      this.$emit("setSearchInput", this.searchInput);
    },
    radioChange() {
      this.$emit("setSearchType", this.searchType);
    },
    activeRouter(item) {
      const vm = this;
      this.activeRoute = item;
    }
  }
};
</script>

<style>
@import url("https://fonts.googleapis.com/css?family=Noto+Sans+TC:300,500,700&display=swap&subset=chinese-traditional");
@import url("../assets/navBar.css");
* {
  padding: 0;
  margin: 0;
  font-family: "Noto Sans TC", sans-serif;
}
</style>