<template>
  <div class="tracks_container">
    <transition-group class="tracksList" name="fade" tag="div">
      <div
        class="track"
        v-for="(item,index) in searchItems"
        :key="index"
        :style="{'transition-delay':index/7+'s'}"
      >
        <div class="track_img_container">
          <div class="img_hidden">
            <div
              class="track_img"
              v-if="kkboxData"
              style="height: 160px;width :160px; background-size: cover; background-position: center"
              :style="{backgroundImage:`url(${item.album.images[0].url})`}"
            ></div>
          </div>
        </div>

        <div class="track_txt">
          <p class="track_name">{{item.name}}</p>
        </div>
        <div class="track_button">
          <button class="playYoutube" @click="playYT(item)">
            <i class="fab fa-youtube track_YT_icon"></i>
          </button>
          <button class="playKKbox" @click="playKkbox(item)">
            <i class="fab fa-kickstarter track_KK_icon"></i>
          </button>
        </div>
      </div>
    </transition-group>
    <!-- <div class="tracksList">
      <div class="track" v-for="(item,index) in searchItems" :key="index">
        <div class="track_img_container">
          <div class="img_hidden">
            <div
              class="track_img"
              v-if="kkboxData"
              style="height: 160px;width :160px; background-size: cover; background-position: center"
              :style="{backgroundImage:`url(${item.album.images[0].url})`}"
            ></div>
          </div>
        </div>

        <div class="track_txt">
          <p class="track_name">{{item.name}}</p>
        </div>
        <div class="track_button">
          <button class="playYoutube" @click="playYT(item)">
            <i class="fab fa-youtube track_YT_icon"></i>
          </button>
          <button class="playKKbox" @click="playKkbox(item)">
            <i class="fab fa-kickstarter track_KK_icon"></i>
          </button>
        </div>
      </div>
    </div>-->
  </div>
</template>

<script>
export default {
  props: ["myKkboxData", "mysearchItems"],
  data() {
    return {
      token: "",
      kkboxData: this.myKkboxData,
      searchItems: this.mysearchItems,
      iframeId: "",
      YTTrackId: ""
    };
  },
  watch: {
    myKkboxData(val) {
      this.kkboxData = val;
    },
    mysearchItems(val) {
      this.searchItems = val;
    }
  },
  computed: {
    bgClosure(index) {
      return function(index) {
        /** do something */
        let bgc = {
          backgroundImage: `linear-gradient(
      -${index * 20}deg,
      #aa52bd 0%,
      #eaccf8 48%,
      #6654f1 100%
    )`
        };
        return bgc;
      };
    }
  },
  methods: {
    playKkbox(item) {
      const vm = this;
      vm.YTTrackId = "";
      vm.iframeId = "";
      vm.$emit("setIframeId", vm.iframeId);
      vm.$emit("setYTTrackId", vm.YTTrackId);
      setTimeout(function() {
        vm.iframeId = item.id;
        vm.$emit("setIframeId", vm.iframeId);
        let KK_iframe_container = document.querySelector(
          ".KK_iframe_container"
        );
        KK_iframe_container.style.opacity = 0.9;
      }, 10);
    },
    playYT(item) {
      const vm = this;
      vm.iframeId = "";
      vm.YTSearchName = "";
      vm.YTTrackId = "";
      vm.$emit("setIframeId", vm.iframeId);
      vm.$emit("setYTTrackId", vm.YTTrackId);
      setTimeout(function() {
        vm.YTSearchName = item.name;
        let TY_iframe_container = document.querySelector(
          ".TY_iframe_container"
        );
        TY_iframe_container.style.opacity = 0.9;
        vm.$http
          .get(
            `https://www.googleapis.com/youtube/v3/search?key=AIzaSyD5X4wGGRG0IyiFPYt66UXKAg-An0ystvQ&part=snippet&type=video&q=${vm.YTSearchName}`
          )
          .then(res => {
            vm.YTTrackId = res.data.items[0].id.videoId;
            vm.$emit("setYTTrackId", vm.YTTrackId);
          });
      }, 100);
    }
  }
};
</script>

<style scope>
@import url("../assets/tracks_container.css");
</style>