<script>
import navBar from "./components/navBar";
import myIframe from "./components/iframe";
export default {
  name: "index",
  components: {
    navBar,
    myIframe
  },
  data() {
    return {
      token: "",
      kkboxData: "",
      searchInput: "",
      searchType: "",
      kkboxCharts: [],
      artistID: "",
      searchItems: "",
      iframeId: "",
      iframeType: "song",
      YTSearchName: "",
      YTTrackId: ""
    };
  },
  computed: {
    chartsFilter() {
      let chartsFilter = [];
      this.kkboxCharts.forEach(function(item) {
        if (chartsFilter.length < 6) {
          chartsFilter.push(item);
        } else return false;
      });
      return chartsFilter;
    }
  },
  methods: {
    getSearchInput(i) {
      this.searchInput = i;
    },
    getsearchType(i) {
      this.searchType = i;
    },
    getsearchItems(i) {
      this.searchItems = i;
    },
    getkkboxData(i) {
      this.kkboxData = i;
    },
    getIframeId(i) {
      this.iframeId = i;
    },
    getYTTrackId(i) {
      this.YTTrackId = i;
    },
    getCharts() {
      let vm = this;
      const config = {
        headers: {
          Authorization: ` Bearer ${vm.token}`
        }
      };
      this.$http
        .get(`https://api.kkbox.com/v1.1/charts?territory=TW`, config)
        .then(res => {
          console.log(res);
          vm.kkboxCharts = res.data.data;
        });
    }
  },
  created() {
    this.getSearchInput();
    this.getsearchItems();
  }
};
</script>

