<template>
  <div class="chart">
    <!-- charts_container -->
    <div class="charts_container">
      <div v-for="item in chartsData" :key="item.id">
        <div
          @click="getChartId(item)"
          class="chart_background"
          style="height: 150px;width :150px; background-size: cover; background-position: center"
          :style="{backgroundImage:`url(${item.images[0].url})`}"
        >
          <div class="bg_matte">
            <p class="data_title">{{item.title}}</p>
          </div>
        </div>
      </div>
    </div>

    <!-- charts_tracks_container -->
    <div class="charts_tracks_container" v-if="chartTracks">
      <!-- title -->
      <div class="title_container">
        <span class="divH2">{{chartTitle}}</span>
      </div>

      <!-- chart_track -->
      <div class="mt30" v-for="item in chart25Tracks" :key="item.id">
        <!-- btn -->
        <div class="track_button">
          <button class="playYoutube" @click="playYT(item)">
            <i class="fab fa-youtube YTicon"></i>
          </button>
          <button class="playKKbox" @click="playKkbox(item)">
            <i class="fab fa-kickstarter KKicon"></i>
          </button>
        </div>
        <!-- track -->
        <div class="chart_track">
          <div class="track_img_radius">
            <div
              class="track_img"
              style="height: 160px;width :160px; background-size: cover; background-position: center"
              :style="{backgroundImage:`url(${item.album.images[0].url})`}"
            ></div>
          </div>

          <p>{{item.name}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["myKkboxData", "mysearchItems"],
  data() {
    return {
      token: "",
      chartsData: "",
      chartId: "",
      chartTracks: "",
      chartTitle: "",
      kkboxData: this.myKkboxData,
      searchItems: this.mysearchItems,
      iframeId: "",
      YTTrackId: ""
    };
  },
  computed: {
    chart25Tracks() {
      const vm = this;
      let tracks = [];
      vm.chartTracks.forEach(function(track) {
        if (tracks.length <= 23) {
          tracks.push(track);
        }
      });
      return tracks;
    }
  },
  methods: {
    getToken() {
      const api =
        "https://cors-anywhere.herokuapp.com/https://account.kkbox.com/oauth2/token";
      const config = {
        headers: {
          Accept: "application/x-www-form-urlencoded",
          "Content-Type": "application/x-www-form-urlencoded"
        }
      };
      const oauth = {
        grant_type: "client_credentials",
        client_id: "fb254aff20b31017a2e84e34ed896523",
        client_secret: "d2f7853d7a7e2afd98bc4751f1b3c81a"
      };
      this.$http.post(api, this.$qs.stringify(oauth), config).then(res => {
        this.token = res.data.access_token;
        this.getChartsData();
      });
    },
    getChartsData() {
      let vm = this;
      const config = {
        headers: {
          Authorization: ` Bearer ${vm.token}`
        }
      };
      this.$http
        .get(`https://api.kkbox.com/v1.1/charts?territory=TW`, config)
        .then(res => {
          vm.chartsData = res.data.data;
        });
    },
    getChartId(item) {
      const vm = this;
      const config = {
        headers: {
          Authorization: ` Bearer ${vm.token}`
        }
      };
      console.log(item.id);
      vm.chartId = item.id;
      this.$http
        .get(
          `https://api.kkbox.com/v1.1/charts/${vm.chartId}?territory=TW`,
          config
        )
        .then(res => {
          console.log(res);
          vm.chartTracks = res.data.tracks.data;
          vm.chartTitle = res.data.title;
        });
    },
    playKkbox(item) {
      const vm = this;
      vm.YTTrackId = "";
      vm.iframeId = "";
      vm.$emit("setIframeId", vm.iframeId);
      vm.$emit("setYTTrackId", vm.YTTrackId);
      setTimeout(function() {
        vm.iframeId = item.id;
        vm.$emit("setIframeId", vm.iframeId);
        let KK_iframe_container = document.querySelector(
          ".KK_iframe_container"
        );
        KK_iframe_container.style.opacity = 0.9;
      }, 10);
    },
    playYT(item) {
      const vm = this;
      vm.iframeId = "";
      vm.YTSearchName = "";
      vm.YTTrackId = "";
      vm.$emit("setIframeId", vm.iframeId);
      vm.$emit("setYTTrackId", vm.YTTrackId);
      setTimeout(function() {
        vm.YTSearchName = item.name;
        let TY_iframe_container = document.querySelector(
          ".TY_iframe_container"
        );
        TY_iframe_container.style.opacity = 0.9;
        vm.$http
          .get(
            `https://www.googleapis.com/youtube/v3/search?key=AIzaSyD5X4wGGRG0IyiFPYt66UXKAg-An0ystvQ&part=snippet&type=video&q=${vm.YTSearchName}`
          )
          .then(res => {
            vm.YTTrackId = res.data.items[0].id.videoId;
            vm.$emit("setYTTrackId", vm.YTTrackId);
          });
      }, 100);
    }
  },
  created() {
    this.getToken();
  }
};
</script>

<style scope>
@import url("../assets/charts.css");
</style>