<template>
  <div class="iframe">
    <!-- KKbox iframe -->
    <div class="KK_iframe_container">
      <div class="close_kkbox" v-if="myKkboxIframeId">
        <span @click="kkboxClose">
          <i class="fas fa-times-circle"></i>
        </span>
      </div>
      <iframe
        class="kkboxIframe"
        v-if="myKkboxIframeId"
        allow="autoplay"
        :src="`https://widget.kkbox.com/v1/?id=${myKkboxIframeId}&type=${iframeType}&terr=TW&lang=TC&autoplay=true`"
        frameborder="0"
      ></iframe>
    </div>

    <!-- YT iframe -->
    <div class="TY_iframe_container">
      <div class="close_YT" v-if="myyoutubeIframeId">
        <span @click="YTClose">
          <i class="fas fa-times-circle"></i>
        </span>
      </div>
      <iframe
        id="player"
        type="text/html"
        v-if="myyoutubeIframeId"
        width="300"
        height="200"
        :src="`https://www.youtube.com/embed/${myyoutubeIframeId}?autoplay=1`"
        frameborder="0"
      ></iframe>
    </div>
  </div>
</template>

<script>
export default {
  props: ["kkboxIframeId", "youtubeIframeId"],
  data() {
    return {
      iframeType: "song",
      myKkboxIframeId: this.kkboxIframeId,
      myyoutubeIframeId: this.youtubeIframeId
    };
  },
  watch: {
    kkboxIframeId: function(val) {
      this.myKkboxIframeId = val;
    },
    youtubeIframeId: function(val) {
      this.myyoutubeIframeId = val;
    }
  },
  methods: {
    kkboxClose() {
      const vm = this;
      vm.myKkboxIframeId = false;
    },
    YTClose() {
      const vm = this;
      vm.myyoutubeIframeId = "";
    }
  }
};
</script>

<style>
@import url("../assets/iframe.css");
</style>